<template>
    <div class="param-fill-wrapper" v-bkloading="{isLoading: templateLoading, opacity: 1}">
        <div class="task-info">
            <h3 class="common-section-title">{{ i18n.taskInfo }}</h3>
            <div class="common-form-item">
                <label class="required">{{ i18n.taskName }}</label>
                <div class="common-form-content">
                    <BaseInput
                        name="taskName"
                        v-model="taskName"
                        v-validate="taskNameRule">
                    </BaseInput>
                    <span class="common-error-tip error-msg">{{ errors.first('taskName') }}</span>
                </div>
            </div>
            <div class="common-form-item" v-if="!common && viewMode === 'app' && userType !== 'functor'">
                <label class="required">{{i18n.startMethod}}</label>
                <div class="common-form-content extra-content">
                    <div class="bk-button-group">
                        <bk-button
                            @click="onChangeStartNow(true)"
                            :type="!isStartNow ? 'default' : 'primary'">
                            {{ i18n.startNow }}
                        </bk-button>
                        <bk-button
                            @click="onChangeStartNow(false)"
                            :type="!isStartNow ? 'primary' : 'default'">
                            {{ i18n.periodicStart }}
                        </bk-button>
                    </div>
                </div>
            </div>
            <div class="common-form-item" v-if="isTaskTypeShow">
                <label class="required">{{ i18n.flowType }}</label>
                <div class="common-form-content">
                    <div class="bk-button-group">
                        <bk-button
                            @click="onSwitchTaskType(false)"
                            :type="isSelectFunctionalType ? 'default' : 'primary'">
                            {{ i18n.defaultFlowType }}
                        </bk-button>
                        <bk-button
                            @click="onSwitchTaskType(true)"
                            :type="isSelectFunctionalType ? 'primary' : 'default'">
                            {{ i18n.functionFlowType }}
                        </bk-button>
                    </div>
                </div>
            </div>
            <div class="common-form-item" v-else-if="viewMode === 'app' && userType !== 'functor'">
                <label class="required">{{i18n.periodicCron}}</label>
                <div class="common-form-content">
                    <BaseInput
                        name="periodicCron"
                        v-model="periodicCron"
                        v-validate="periodicRule"/>
                    <bk-tooltip placement="left-end" class="periodic-img-tooltip" v-bkloading="{isLoading: templateLoading, opacity: 1}">
                        <i class="common-icon-warning"></i>
                        <div slot="content">
                            <img :src="periodicCronImg">
                        </div>
                    </bk-tooltip>
                    <span v-show="errors.has('periodicCron')" class="common-error-tip error-msg">{{ errors.first('periodicCron') }}</span>
                </div>
            </div>
        </div>
        <div class="param-info">
            <h3 class="common-section-title">{{ i18n.paramsInfo }}</h3>
            <template v-if="!templateLoading">
                <NoData v-if="isVariableEmpty"></NoData>
                <TaskParamEdit
                    v-else
                    ref="TaskParamEdit"
                    :constants="pipelineData.constants">
                </TaskParamEdit>
            </template>
        </div>
        <div v-if="!templateLoading" class="action-wrapper">
            <bk-button
                @click="onShowPreviewDialog">
                {{ i18n.preview }}
            </bk-button>
            <bk-button
                type="primary"
                @click="onGotoSelectNode">
                {{ i18n.previous }}
            </bk-button>
            <bk-button type="success" @click="onCreateTask">{{ i18n.new }}</bk-button>
        </div>
        <bk-dialog
            v-if="previewDialogShow"
            :quick-close="false"
            :has-header="true"
            :has-footer="false"
            :ext-cls="'common-dialog'"
            :title="i18n.taskPreview"
            width="1000"
            :is-show.sync="previewDialogShow"
            @cancel="onCancel">
            <div slot="content">
                <NodePreview
                    ref="nodePreviewRef"
                    :previewDataLoading="previewDataLoading"
                    :canvasData="formatCanvasData(previewData)"
                    :previewBread="previewBread"
                    @onNodeClick="onNodeClick"
                    @onSelectSubflow="onSelectSubflow">
                </NodePreview>
            </div>
        </bk-dialog>
    </div>
</template>
<script>
import '@/utils/i18n.js'
// moment
import moment from 'moment-timezone'
import { mapState, mapActions, mapMutations } from 'vuex'
import { errorHandler } from '@/utils/errorHandler.js'
import { NAME_REG, PERIODIC_REG, STRING_LENGTH } from '@/constants/index.js'
import tools from '@/utils/tools.js'
import NoData from '@/components/common/base/NoData.vue'
import BaseInput from '@/components/common/base/BaseInput.vue'
import TaskParamEdit from '../TaskParamEdit.vue'
import NodePreview from '../NodePreview.vue'

export default {
    name: 'TaskParamFill',
    components: {
        NoData,
        BaseInput,
        TaskParamEdit,
        NodePreview
    },
    props: ['cc_id', 'template_id', 'excludeNode', 'common'],
    data () {
        return {
            i18n: {
                taskInfo: gettext(""),
                taskName: gettext(""),
                flowType: gettext(""),
                defaultFlowType: gettext(""),
                functionFlowType: gettext(""),
                paramsInfo: gettext(""),
                preview: gettext(""),
                previous: gettext(""),
                new: gettext(""),
                taskPreview: gettext(""),
                startNow: gettext(''),
                periodicStart: gettext(''),
                periodicCron: gettext(''),
                startMethod: gettext('')
            },
            templateLoading: true,
            bkMessageInstance: null,
            previewDataLoading: true,
            isSubmit: false,
            isSelectFunctionalType: false,
            previewDialogShow: false,
            taskName: '',
            previewData: {
                location: [],
                line: [],
                gateways: {},
                constants: []
            },
            pipelineData: {},
            taskNameRule: {
                required: true,
                max: STRING_LENGTH.TASK_NAME_MAX_LENGTH,
                regex: NAME_REG
            },
            isStartNow: true,
            periodicCron: '*/5 * * * *',
            periodicRule: {
                required: true,
                regex: PERIODIC_REG
            },
            periodicCronImg: require('@/assets/images/' + gettext('task-zh') + '.png'),
            lastTaskName: '',
            node: {},
            templateData: {},
            previewBread: [],
            version: ''
        }
    },
    computed: {
        ...mapState({
            'templateName': state => state.template.name,
            'userType': state => state.userType,
            'viewMode': state => state.view_mode,
            'run_ver': state => state.run_ver,
            'app_id': state => state.app_id,
            'businessTimezone': state => state.businessTimezone
        }),
        isSchemeShow () {
            return this.pipelineData.location.some(item => item.optional)
        },
        isTaskTypeShow () {
            return this.userType !== 'functor' && this.isStartNow
        },
        isVariableEmpty () {
            return !this.pipelineData.constants || Object.keys(this.pipelineData.constants).length === 0
        }
    },
    mounted () {
        this.loadData()
    },
    methods: {
        ...mapActions('template/', [
            'loadTemplateData'
        ]),
        ...mapActions('task/', [
            'loadPreviewNodeData',
            'createTask'
        ]),
        ...mapMutations('template/', [
            'setTemplateData'
        ]),
        ...mapActions('periodic/',[
            'createPeriodic'
        ]),
        async loadData () {
            this.templateLoading = true
            try {
                const data = {
                    templateId: this.template_id,
                    common: this.common
                }
                const templateData = await this.loadTemplateData(data)
                this.version = this.templateData.version
                await this.getPreviewNodeData(this.template_id)
                this.setTemplateData(templateData)
                this.pipelineData = tools.deepClone(this.previewData)
                this.taskName = this.getDefaultTaskName()
            } catch (e) {
                errorHandler(e, this)
            }
        },
        /**
         * ()
         *
         * @params {String} template_id   ID
         * @params {Boolean} isSubflow  
         */
        async getPreviewNodeData (template_id, isSubflow = false) {
            this.previewDataLoading = true
            const excludeNode = isSubflow ? [] : this.excludeNode
            const params = {
                templateId: template_id,
                excludeTaskNodesId: JSON.stringify(excludeNode),
                common: this.common,
                cc_id: this.cc_id,
                template_source: 'business',
                version: this.version
            }
            try {
                const resp = await this.loadPreviewNodeData(params)
                if (resp.result) {
                    let previewNodeData = resp.data.pipeline_tree
                    let compareNodeData = JSON.stringify(previewNodeData)
                    while (true) {
                        this.clearUnnecessaryGateway(previewNodeData)
                        if (compareNodeData === JSON.stringify(previewNodeData)) {
                            break
                        }
                        compareNodeData = JSON.stringify(previewNodeData)
                    }
                    this.previewData = previewNodeData
                } else {
                    errorHandler(resp, this)
                }
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.templateLoading = false
                this.previewDataLoading = false
            }
        },
        getDefaultTaskName () {
            let nowTime = ''
            if (this.common) {
                // 
                nowTime = moment().format('YYYYMMDDHHmmss')
            } else {
                nowTime = moment.tz(this.businessTimezone).format('YYYYMMDDHHmmss')
            }
            return this.templateName + '_' + nowTime
        },
        formatCanvasData (data) {
            const {lines, locations, gateways} = data
            const branchConditions = {}
            for (let gKey in gateways) {
                const item = gateways[gKey]
                if (item.conditions) {
                    branchConditions[item.id] = Object.assign({}, item.conditions)
                }
            }
            return {
                lines: data.line,
                locations: data.location.map(item => {return {...item, mode: 'preview', checked: true}}),
                branchConditions
            }
        },
        updateCanvas () {
            this.previewDataLoading = true
            this.$nextTick(() => {
                this.previewDataLoading = false
            })
        },
        onSwitchTaskType (isSelectFunctionalType) {
            this.isSelectFunctionalType = isSelectFunctionalType
            this.$emit('setFunctionalStep', isSelectFunctionalType)
        },
        onShowPreviewDialog () {
            this.previewBread.push({
                data: this.template_id,
                name: this.taskName
            })
            this.previewDialogShow = true
        },
        onCancel () {
            this.previewDialogShow = false
            this.previewDataLoading = false
            this.previewData = tools.deepClone(this.pipelineData)
            this.previewBread = []
        },
        onGotoSelectNode () {
            this.$emit('setFunctionalStep', false)
            if (this.viewMode === 'appmaker') {
                this.$router.push({path: `/appmaker/${this.app_id}/newtask/${this.cc_id}/selectnode/`, query: {'template_id': this.template_id}})
            } else {
                if (this.common) {
                    this.$router.push({path: `/template/newtask/${this.cc_id}/selectnode/`, query: {'template_id': this.template_id, common: this.common}})
                } else {
                    this.$router.push({path: `/template/newtask/${this.cc_id}/selectnode/`, query: {'template_id': this.template_id}})
                }
            }
        },
        onCreateTask () {
            if (this.isSubmit) return
            const paramEditComp = this.$refs.TaskParamEdit
            this.$validator.validateAll().then(async (result) => {
                let formValid = true
                const pipelineData = tools.deepClone(this.pipelineData)
                if (paramEditComp) {
                    const formData = paramEditComp.getVariableData()
                    pipelineData.constants = formData
                    formValid = paramEditComp.validate()
                }

                if (!result || !formValid) return

                this.isSubmit = true
                let flowType
                if (this.userType === 'functor') {
                    flowType = 'common_func'
                } else {
                    flowType = this.isSelectFunctionalType ? 'common_func' : 'common'
                }
                if (this.isStartNow) {
                    const data = {
                        "name": this.taskName,
                        "description": '',
                        "templateId": this.template_id,
                        "execData": JSON.stringify(pipelineData),
                        "flowType": flowType,
                        'common': this.common
                    }
                    try {
                        const taskData = await this.createTask(data)
                        if (this.viewMode === 'appmaker') {
                            if (this.isSelectFunctionalType) {
                                this.$router.push({path: `/appmaker/${this.app_id}/task_home/${this.cc_id}/`})
                            }
                            this.$router.push({path: `/appmaker/${this.app_id}/execute/${this.cc_id}/`, query: {instance_id: taskData.instance_id}})
                        }
                        if (this.isSelectFunctionalType) {
                            if (this.common) {
                                this.$router.push({path: `/taskflow/home/${this.cc_id}/`, query: {common: this.common}})
                            } else {
                                this.$router.push({path: `/taskflow/home/${this.cc_id}/`})
                            }
                        } else {
                            this.$router.push({path: `/taskflow/execute/${this.cc_id}/`, query: {instance_id: taskData.instance_id}})
                        }
                    } catch (e) {
                        errorHandler(e, this)
                    }
                } else {
                    // 
                    const cronArray = this.periodicCron.split(' ')
                    const cron = JSON.stringify({
                        'minute': cronArray[0],
                        'hour': cronArray[1],
                        'day_of_week': cronArray[2],
                        'day_of_month': cronArray[3],
                        'month_of_year': cronArray[4]
                    })
                    const data = {
                        "name": this.taskName,
                        "cron": cron,
                        "templateId": this.template_id,
                        "execData": JSON.stringify(pipelineData)
                    }
                    const periodicData = await this.createPeriodic(data)
                    this.$bkMessage({
                        'message': gettext(''),
                        'theme': 'success'
                    })
                    this.$router.push({path: `/periodic/home/${this.cc_id}/`})
                }
            })
        },
        onChangeStartNow (value) {
            if (this.viewMode !== 'app' || this.userType === 'functor' || value === this.isStartNow) {
                return
            }
            this.isStartNow = value
            this.$emit('setPeriodicStep', {'periodicType': value, 'functionalType': this.isSelectFunctionalType})
            if (!value) {
                this.lastTaskName = this.taskName
                this.taskName = this.templateName
            } else {
                this.taskName = this.lastTaskName
            }
        },
        onSelectSubflow (id, index) {
            if (id === this.template_id) {
                this.previewData = this.pipelineData
                this.updateCanvas()
            } else {
                this.getPreviewNodeData(id, true)
            }
            this.previewBread.splice(index + 1, this.previewBread.length)
        },
        onNodeClick (id) {
            const activity = this.previewData.activities[id]
            if (!activity || activity.type !== 'SubProcess') {
                return
            }

            const templateId = activity.template_id
            this.previewBread.push({
                data: templateId,
                name: activity.name
            })
            this.getPreviewNodeData(templateId, true)
        },
        clearUnnecessaryGateway (previewNodeData) {
            const {line, location}  = previewNodeData
            //  
            let group = []
            for (let nowLine of line) {
                let sourceId = nowLine.source.id
                let targetId = nowLine.target.id
                if (group[sourceId]) {
                    group[sourceId].push(targetId)
                } else {
                    group[sourceId] = [targetId]
                }
            }
            location.forEach((nowLocation) => {
                const {type, id} = nowLocation
                if (type === 'parallelgateway' || type === 'branchgateway') {
                    // 
                    let lineArray = []
                    // Array
                    group[id].forEach((targetId) => {
                        if (lineArray.indexOf(targetId) === -1) {
                            lineArray.push(targetId)
                        }
                    })
                    const lineArrayLength =  lineArray.length
                    let deleteLocation = location.find(location => location.id === lineArray[0])
                    let deleteLocationType = undefined
                    if (deleteLocation !== undefined) {
                        deleteLocationType = deleteLocation.type
                    }
                    // 1 
                    if (lineArrayLength === 1 &&  deleteLocationType !== 'subflow' && deleteLocationType !== 'tasknode') {
                        // id
                        let convergegatewayId =  group[id][0]
                        const convergegateway = location.find(location => location.id === convergegatewayId)
                        if (!convergegateway || convergegateway.type !== 'convergegateway') {
                            // 
                            return
                        }
                        // 
                        let lastId = line.find((newLine) => newLine.source.id === convergegatewayId).target.id
                        // 
                        let preId = line.find((newLine) => newLine.target.id === id).source.id
                        line.find((newLine) => newLine.source.id === preId && newLine.target.id === id).target.id = lastId
                        // 
                        while (true) {
                            // 
                            const frontLineSourceIndex = line.findIndex(item => item.source.id === id)
                            if (frontLineSourceIndex !== -1) {
                                line.splice(frontLineSourceIndex, 1)
                            }
                            // 
                            const lastLineTargetIndex = line.findIndex(item => item.target.id === convergegatewayId)
                            if (lastLineTargetIndex !== -1) {
                                line.splice(lastLineTargetIndex, 1)
                            }
                            // 
                            const lastLineSourceIndex = line.findIndex(item => item.source.id === convergegatewayId)
                            if (lastLineSourceIndex !== -1) {
                                line.splice(lastLineSourceIndex, 1)
                            }

                            if (frontLineSourceIndex === -1 && lastLineTargetIndex === -1 && lastLineSourceIndex === -1) {
                                break
                            }
                        }
                        // 
                        const lastLocationIndex = location.findIndex(item => item.id === convergegatewayId)
                        location.splice(lastLocationIndex, 1)
                        const frontLocationIndex = location.findIndex(item => item.id === id)
                        location.splice(frontLocationIndex, 1)
                        // 
                    } else {
                        // 
                        lineArray.forEach((newLocationId) => {
                            let deleteLocation = location.find((newLocation) => newLocation.id === newLocationId && newLocation.type === 'convergegateway')
                            if (deleteLocation) {
                                for (let locationId of group[id]) {
                                    // 
                                    if (locationId === newLocationId) {
                                        const frontLineSourceIndex = line.findIndex(item => item.target.id === newLocationId && item.source.id === id)
                                        line.splice(frontLineSourceIndex, 1)
                                    }
                                }
                            }
                        })
                    }
                }
            })
            // 
            return Object.assign(previewNodeData, {line, location})
        }
    }
}
</script>
